import React, { useRef, useLayoutEffect,useState } from 'react';
// import logo from './logo.svg';
import Loading from './Loading'
import Divider from '@material-ui/core/Divider';
// import SimplePaper from './SimplePaper'
import './App.css';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";
import { useQuery } from '@apollo/react-hooks';
import gql from "graphql-tag";
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';
// import Paper from '@material-ui/core/Paper';
// import { makeStyles } from '@material-ui/core/styles';
// import clsx from 'clsx';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
// import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
// import CardActions from '@material-ui/core/CardActions';
// import Collapse from '@material-ui/core/Collapse';
// import Avatar from '@material-ui/core/Avatar';
// import IconButton from '@material-ui/core/IconButton';
// import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';
// import FavoriteIcon from '@material-ui/icons/Favorite';
// import ShareIcon from '@material-ui/icons/Share';
// import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
// import MoreVertIcon from '@material-ui/icons/MoreVert';
am4core.useTheme(am4themes_animated);

const useStyles = makeStyles((theme) => ({
    root: {
      maxWidth: '100%',

    },
    header:{
      background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
      borderRadius: 3,
      border: 0,
      color: 'white',
      height: 48,
      padding: '0 30px',
      boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
    },
    media: {
      height: 0,
      paddingTop: '25.25%', // 16:9
    },
    expand: {
      transform: 'rotate(0deg)',
      marginLeft: 'auto',
      transition: theme.transitions.create('transform', {
        duration: theme.transitions.duration.shortest,
      }),
    },
    expandOpen: {
      transform: 'rotate(180deg)',
    },
    avatar: {
      backgroundColor: red[500],
    },
    button:{
      textAlign:"left"
    }
  }));


const data1=  gql`   
    query{
           actuals{
               Date,Confirmed,Recovered,Deceased,Type
             }
         }
       `


function CreateChart(props) {
  const chart = useRef(null);
  const classes = useStyles();
  let itemtitle=''
  console.log(`In CreateChart: ${props.btnname}`)
  if (props.btnname==='Confirmed') {
   itemtitle ="Covid Confirmed Cases Trend"
  } else if(props.btnname==='Recovered'){
   itemtitle="Covid Recovered Cases Trend"
  } else {
   itemtitle="Covid Death Cases Trend"
  }
  useLayoutEffect(() => {
    let x = am4core.create("chartdiv", am4charts.XYChart);
   
    x.paddingRight = 20;

    // let data = [];
    // let visits = 10;

    // for (let i = 1; i < 366; i++) {
    //   visits += Math.round((Math.random() < 0.5 ? 1 : -1) * Math.random() * 10);
    //   data.push({ date: new Date(2018, 0, i), name: "name" + i, value: visits });
    // }

    x.data = props.data;

    let dateAxis = x.xAxes.push(new am4charts.DateAxis());
    dateAxis.renderer.grid.template.location = 0;

    let valueAxis = x.yAxes.push(new am4charts.ValueAxis());
    valueAxis.tooltip.disabled = true;
    valueAxis.renderer.minWidth = 35;

    let series = x.series.push(new am4charts.LineSeries());
    series.dataFields.dateX = "Date";
    series.dataFields.valueY = props.btnname;
    series.tooltipText = "{valueY.value}";
    x.cursor = new am4charts.XYCursor();

    let scrollbarX = new am4charts.XYChartScrollbar();
    scrollbarX.series.push(series);
    x.scrollbarX = scrollbarX;
    var range = dateAxis.axisRanges.push(new am4charts.DateAxisDataItem());
    range.grid.stroke = x.colors.getIndex(0);
    range.grid.strokeOpacity = 1;
   
    var seriesRange = dateAxis.createSeriesRange(series);
    seriesRange.contents.strokeDasharray = "2,3";
    seriesRange.contents.stroke = x.colors.getIndex(8);
    seriesRange.contents.strokeWidth = 1;

range.bullet = new am4core.ResizeButton();
range.bullet.background.fill = x.colors.getIndex(0);
range.bullet.background.states.copyFrom(x.zoomOutButton.background.states);
range.bullet.minX = 0;
range.bullet.adapter.add("minY", function(minY, target) {
  target.maxY = x.plotContainer.maxHeight;
  target.maxX = x.plotContainer.maxWidth;
  return x.plotContainer.maxHeight;
})


// range.bullet.events.on("dragged", function() {
//   range.value = dateAxis.xToValue(range.bullet.pixelX);
//   seriesRange.value = range.value;
// })
let min_act=props.data.reduce(
  (olddt,newdt)=> 
  (new Date(olddt.Date) && olddt.Type==='Acutals') > (new Date(newdt.Date) && newdt.Type==='Acutals')
  ? olddt:newdt)


  let max_act=props.data.reduce(
    (olddt,newdt)=> 
    (new Date(olddt.Date) && olddt.Type==='Acutals') < (new Date(newdt.Date) && newdt.Type==='Acutals')
    ? olddt:newdt)
  
console.log(`min_act:${JSON.stringify(min_act)} mix_act${JSON.stringify(max_act)}`)

range.date = new Date(min_act.Date);
seriesRange.date = new Date(min_act.Date);
seriesRange.endDate = new Date(max_act.Date)







    chart.current = x;

    return () => {
      x.dispose();
    };
  },[props.btnname,props.data] );

  return (

  <div>
 <Card className={classes.root}>

 <CardHeader className={classes.header}
  // style={{Color: "#000", Background: "blue"}}
        title={itemtitle}
        subheader="For the Year 2020"
      />
 
 <Divider />
    <CardContent  id="chartdiv" style={{ width: "100%", height: "500px" }}/> 
    </Card>

    </div>
  );
}


 const  ActualForecast = () => {
    const { loading, error, data } = useQuery(data1);
 const [state,setState]= useState('Confirmed');

 const classes = useStyles();

    if (loading) {
      return <div className='loader_graph'>Loading...</div>;
    }
    if (error) {
      console.error(error);
      return  <div>Error</div>;
    }

   
    const onClickHandler = (btnname,data) =>{
      setState(btnname)
      // console.log(`btn clicked ${btnname} ${JSON.stringify(data)}`)
    //  return <Test data={data} btnname={state} />
    }
    return (
    
   
    <>
    <div>
     <br/><br/><br/><br/>
    <ButtonGroup color="secondary" aria-label="outlined secondary button group">
        <Button onClick={()=>onClickHandler("Confirmed",data.actuals)}>Confirmed</Button>
        <Button onClick={()=>onClickHandler("Recovered",data.actuals)}>Recovered</Button>
        <Button onClick={()=>onClickHandler("Deceased",data.actuals)}>Deceased</Button>
      </ButtonGroup>
      <CreateChart data={data.actuals} btnname={state} />
      </div>
      </>
      
      )
  };

 
   

export default ActualForecast;