function CreateChart(props) {
  const chartcurr = useRef(null);
  const classes = useStyles();
//   let itemtitle=''
//   console.log(`In CreateChart: ${props.btnname}`)
//   if (props.btnname==='Summary') {
//    itemtitle ="Latest Day Mobility Report"
//   } else {
//    itemtitle="Mobility Report Trends"
//   }
  useLayoutEffect(() => {
    var chart = am4core.create(props.titlename, am4charts.XYChart);

    chart.width = am4core.percent(100);
    chart.height = am4core.percent(100);
    console.log(`titlename:${props.titlename}`)
    
// Add data
chart.data = props.data;
console.log(`chart.data[0]: ${JSON.stringify(chart.data[0].Date)}`)
// Create axes
var dateAxis = chart.xAxes.push(new am4charts.DateAxis());
// dateAxis.renderer.opposite = true;
var valueAxis = chart.yAxes.push(new am4charts.ValueAxis());

// Create series
var series = chart.series.push(new am4charts.LineSeries());
series.dataFields.valueY = props.titlename;
series.dataFields.dateX = "Date";
series.strokeWidth = 1;
series.minBulletDistance = 10;
series.tooltipText = "{valueY.value}";
series.fillOpacity = 0.1;
series.tooltip.pointerOrientation = "vertical";
series.tooltip.background.cornerRadius = 20;
series.tooltip.background.fillOpacity = 0.5;
series.tooltip.label.padding(12, 12, 12, 12)

var seriesRange = dateAxis.createSeriesRange(series);
seriesRange.contents.strokeDasharray = "2,3";
seriesRange.contents.stroke = chart.colors.getIndex(8);
seriesRange.contents.strokeWidth = 1;

var pattern = new am4core.LinePattern();
pattern.rotation = -45;
pattern.stroke = seriesRange.contents.stroke;
pattern.width = 100;
pattern.height = 100;
pattern.gap = 6;
seriesRange.contents.fill = pattern;
seriesRange.contents.fillOpacity = 0.5;

// Add scrollbar
chart.scrollbarX = new am4core.Scrollbar();

// add range
var range = dateAxis.axisRanges.push(new am4charts.DateAxisDataItem());
range.grid.stroke = chart.colors.getIndex(0);
range.grid.strokeOpacity = 1;
// range.bullet = new am4core.ResizeButton();
// range.bullet.background.fill = chart.colors.getIndex(0);
// range.bullet.background.states.copyFrom(chart.zoomOutButton.background.states);
// range.bullet.minX = 0;
// range.bullet.adapter.add("minY", function(minY, target) {
//   target.maxY = chart.plotContainer.maxHeight;
//   target.maxX = chart.plotContainer.maxWidth;
//   return chart.plotContainer.maxHeight;
// })

// range.bullet.events.on("dragged", function() {
//   range.value = dateAxis.xToValue(range.bullet.pixelX);
//   seriesRange.value = range.value;
// })


// var firstTime = new Date(chart.data[0].Date).getTime();
// var lastTime = new Date(chart.data[chart.data.length - 1].Date).getTime();
// var date = new Date(firstTime + (lastTime - firstTime) / 2);

// range.date = date;

// seriesRange.date = date;
// seriesRange.endDate = new Date(chart.data[chart.data.length - 1].Date);

    chartcurr.current = chart;

    return () => {
      chart.dispose();
    };
  },[props.titlename,props.data]);

  return (

  <div>
 <Card className={classes.root}>

 <CardHeader className={classes.header}
        title={props.titlename.replace("_"," ")}
        
        // subheader="For the Year 2020"
      />
 
 <Divider />
    <CardContent  id={props.titlename} style={{ width: "100%", height: "200px" }}/> 
    </Card>

    </div>
  );
}

function CreateChartsummary(props) {
    const chartcurr = useRef(null);
    const classes = useStyles();
    // let itemtitle=''
    // console.log(`In CreateChart: ${props.btnname}`)
    // if (props.btnname==='Summary') {
    //  itemtitle ="Latest Day Mobility Report"
    // } else {
    //  itemtitle="Mobility Report Trends"
    // }
    useLayoutEffect(() => {
      var chart = am4core.create(props.titlename, am4charts.XYChart);
  
      chart.hiddenState.properties.opacity = 0; // this makes initial fade in effect

      chart.data = props.data;
      
      
      var categoryAxis = chart.xAxes.push(new am4charts.CategoryAxis());
      categoryAxis.renderer.grid.template.location = 0;
      categoryAxis.dataFields.category = "Category";
      categoryAxis.renderer.minGridDistance = 40;
      
      var valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
      
      var series = chart.series.push(new am4charts.CurvedColumnSeries());
      series.dataFields.categoryX = "Category";
      series.dataFields.valueY = "Value";
      series.tooltipText = "{valueY.Value}"
      series.columns.template.strokeOpacity = 0;
      
      series.columns.template.fillOpacity = 0.75;
      
      var hoverState = series.columns.template.states.create("hover");
      hoverState.properties.fillOpacity = 1;
      hoverState.properties.tension = 0.4;
      
      chart.cursor = new am4charts.XYCursor();
      
      // Add distinctive colors for each column using adapter
      series.columns.template.adapter.add("fill", function(fill, target) {
        return chart.colors.getIndex(target.dataItem.index);
      });
      
    //   chart.scrollbarX = new am4core.Scrollbar();
    //   // chartcurr.current = chart;
  
    return () => {
      chart.dispose();
    };
  }, [props.data,props.titlename]);

    return (
  
    <div>
   <Card className={classes.root}>
  
   <CardHeader className={classes.header}
          title={props.titlename.replace("_"," ")}
          
          // subheader="For the Year 2020"
        />
   
   <Divider />
      <CardContent  id={props.titlename} style={{ width: "100%", height: "500px" }}/> 
      </Card>
  
      </div>
    );
  }
  

 const  MS = () => {
    const classes = useStyles();
    const [state,setState]= useState('Summary');
    const { loading, error, data } = useQuery(mobility);
    // const { loading, error, data } = useQuery(mobilitysummary);
    console.log(`mobilitysummmary: ${error}`)
   
   
    if (loading) {
      return <div>< Loading/></div>;
    }
    if (error) {
      console.error(error);
      return  <div>Error</div>;
    }

    console.log(`mobility data${JSON.stringify(data.mobility_summary)}`)
    const onClickHandler = (btnname) =>{
      setState(btnname)
      console.log(`btn clicked ${btnname} ${JSON.stringify(data.mobility_summary)}`)
    //  return <Test data={data} btnname={state} />
    }
    return (
    
   
    <>
    <div>
     <br/><br/><br/><br/>
    <ButtonGroup color="secondary" aria-label="outlined secondary button group">
        <Button onClick={()=>onClickHandler("Summary")}>Summery</Button>
        <Button onClick={()=>onClickHandler("Details")}>Details</Button>
      </ButtonGroup>
      <div>
    <Card className={classes.root}>

    <CardHeader className={classes.headermain}
            title={"Mobility Analytics"}
        />
    <Divider />
    </Card>

    </div>
      <br/>
      <div className={classes.root}>
          <Grid container spacing={2}
          justify="center"
          >
            
            { state==='Summary' ? (
              
              <Grid item xs={12} sm={12} >
             <CreateChartsummary data={data.mobility_summary} btnname={state} titlename={state}/>
             </Grid>):
          (Object.keys(data.mobility[0]).slice(1,-1).map((prop, key)=>{ return (<Grid item xs={3} sm={4} key={key}>
         <CreateChart data={data.mobility} btnname={state} titlename={prop}/>
         </Grid>)}))}
          </Grid>
        
        </div>

      {/* <CreateChart data={data.mobility} btnname={state} /> */}
      </div>
      </>
      
      )
  };

 
   

export default MS;